import React, { useState, useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';
import { ActionSheetItem, ActionSheet, IOS, platform, Input, ANDROID, ModalRoot, ModalPage, ModalPageHeader, PanelHeaderButton, FormLayout, FormLayoutGroup, Cell, Div } from '@vkontakte/vkui'
import View from '@vkontakte/vkui/dist/components/View/View';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import Icon24Cancel from '@vkontakte/icons/dist/24/cancel';
import Icon24Done from '@vkontakte/icons/dist/24/done';
import Icon24Crop from '@vkontakte/icons/dist/24/crop';
import Icon24Copy from '@vkontakte/icons/dist/24/copy';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';
import Persik from './panels/Persik';
const osname = platform();
const MODAL_PAGE_FILTERS = 'filters';

class App extends React.Component {
	constructor(props) {
		super(props);
			this.state = {
					activePanel: 'home',
					fetchedUser: null,
					popout: <ScreenSpinner size='large' />,
					activeModal: null,
					modalHistory: []
                }
				    this.modalBack = () => {
					this.setActiveModal(this.state.modalHistory[this.state.modalHistory.length - 2]);
				};
             }

		setActiveModal(activeModal) {
			activeModal = activeModal || null;
			let modalHistory = this.state.modalHistory ? [...this.state.modalHistory] : [];

			if (activeModal === null) {
				modalHistory = [];
			} else if (modalHistory.indexOf(activeModal) !== -1) {
				modalHistory = modalHistory.splice(0, modalHistory.indexOf(activeModal) + 1);
			} else {
				modalHistory.push(activeModal);
			}

			this.setState({
			activeModal,
			modalHistory
		});
		}

	useEffect() {
		bridge.subscribe(({ detail: { type, data }}) => {
			if (type === 'VKWebAppUpdateConfig') {
				const schemeAttribute = document.createAttribute('scheme');
				schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
				document.body.attributes.setNamedItem(schemeAttribute);
			}
		});
	};
	
	componentDidMount() {
	    bridge.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppGetUserInfoResult':
            this.setState({ fetchedUser: e.detail.data });
			this.setState({ popout: null });
			break;
			}
		})
		bridge.send('VKWebAppGetUserInfo', {});
	}

	go = (e) => {
		this.setState({ activePanel: e.currentTarget.dataset.to })
	};

    render() {
		const modal = (
			<ModalRoot
			activeModal={this.state.activeModal}
			onClose={this.modalBack}
		>
			<ModalPage
				id={MODAL_PAGE_FILTERS}
				onClose={this.modalBack}
				header={
            <ModalPageHeader
              left={<PanelHeaderButton onClick={this.modalBack}>{osname === ANDROID ? 'Отмена' :<Icon24Cancel />}</PanelHeaderButton>}
            >
              Скупка PaperScroll
            </ModalPageHeader>
          }
        >
          <FormLayout>
		       <FormLayoutGroup top="Введите количество PaperScroll">
              <Input value="100" />
			  </FormLayoutGroup>
			  <FormLayoutGroup>
				<Cell indicator={<Icon24Crop />}>Номер телефона: +7987654321</Cell>
					{this.state.fetchedUser && <Cell asideContent={<Icon24Copy />}>{`Комментарий: ${this.state.fetchedUser.id}`}</Cell>}
			  </FormLayoutGroup>
		  </FormLayout>
			</ModalPage>
			</ModalRoot>)
			return (
				<View activePanel={this.state.activePanel} popout={this.state.popout} modal={modal}>
					<Home id='home' fetchedUser={this.state.fetchedUser} go={this.go} openKupit={() => this.setActiveModal(MODAL_PAGE_FILTERS)}/>
					<Persik id='persik' go={this.go} />
				</View>
	    );
}
}

export default App;

